<template>
    <div class="main">
        <div class="archivesManagement">
            <div class="searchA">
                <el-input style="width:300px" placeholder="请输入姓名" v-model="search.student_name" clearable size="small"></el-input>
                <el-input style="width:300px" placeholder="请输入班级" v-model="search.class_name" clearable size="small"></el-input>
                <el-button size="small" type="primary" plain icon="el-icon-search" @click="serchList()">查询</el-button>
                <!-- <div class="add">
                    <el-button size="small" type="primary" icon="el-icon-plus" @click="addBtn()">新增</el-button>
                </div> -->
            </div>
            <div class="home_table">
                <el-table :data="tableData" border v-loading="isLoading" element-loading-background="rgba(0, 0, 0, 0.4)" size="medium" :max-height="tableHeight" style="width: 100%">
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true" v-if="item.prop == 'disabled'">
                            <template slot-scope="scope">
                                <el-tag size="small" :type="scope.row.disabled==0?'':'info'">{{scope.row.disabled==0?'正常':'禁用'}}</el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :show-overflow-tooltip="true" :min-width="item.minWidth" v-else></el-table-column>
                    </template>
                    <el-table-column prop="portrait" align="center" label="学生素质画像" fixed="right" width="120">
                        <template slot-scope="scope">
                            <el-button @click="checkPortrait(scope.row)" type="text" size="small">查看</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column prop="archives" align="center" label="查看档案" fixed="right" width="120">
                        <template slot-scope="scope">
                            <el-button @click="checkArchives(scope.row)" type="text" size="small">查看</el-button>
                        </template>
                    </el-table-column>
                    <el-table-column prop="address" align="center" label="查看基础信息" fixed="right" width="120">
                        <template slot-scope="scope">
                            <el-button @click="checkInfo(scope.row)" type="text" size="small">查看</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="pagination">
                    <el-pagination background :current-page.sync="pageNum" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="total, sizes, prev, pager, next" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
                </div>
            </div>
            <!-- 新增编辑弹窗 -->
            <el-dialog title="查看基础信息" v-dialogDrag :visible.sync="dialogVisible" width="600px" :close-on-click-modal="false">
                <el-form :model="formData" size="medium" ref="formData" :rules="rule" label-width="110px">
                    <el-form-item label="学生姓名">{{formData.student_name}}</el-form-item>
                    <el-form-item label="性别">{{formData.sex_name}}</el-form-item>
                    <el-form-item label="班级">{{formData.class_name}}</el-form-item>
                    <el-form-item label="身份证号">{{formData.id_card}}</el-form-item>
                    <el-form-item label="学籍号">{{formData.student_code}}</el-form-item>
                    <el-form-item label="家长姓名">{{formData.guardian_name}}</el-form-item>
                    <el-form-item label="联系电话">{{formData.guardian_tel}}</el-form-item>
                    <el-form-item label="住址">{{formData.address}}</el-form-item>
                </el-form>
            </el-dialog>
            <!-- 新增编辑弹窗 -->
            <el-dialog title="画像信息" v-dialogDrag :visible.sync="portraitVisible" width="600px" :close-on-click-modal="false">
                <div ref="portrait" style="width:100%;height:376px"></div>
            </el-dialog>
        </div>
    </div>
</template>
<script>
import {
    archivesManagementList,
    archivesManagementArchivesList,
    archivesManagementArchivesInfo,
    archivesManagementArchivesBasicInfo,
    hashApi,
} from '@/api/index';

export default {
    data() {
        return {
            total: 0,
            pageNum: 1,
            pageSize: 15,
            isLoading: false,
            tableHeight: window.innerHeight - 180,
            formData: {},
            method_auth: {},
            editId: '',
            title: '',
            search: {
                class_name: '',
                student_name: "",
            },
            dialogVisible: false,
            portraitVisible: false,
            portrait: [],
            rule: {
                student_name: [
                    { required: true, message: '请输入学生姓名', trigger: 'blur' },
                ],
                sex: [
                    { required: true, message: '请选择性别', trigger: 'change' }
                ],
                grade_id: [
                    { required: true, message: '请选择所在年级', trigger: 'change' }
                ],
                class_id: [
                    { required: true, message: '请选择所在班级', trigger: 'change' }
                ],
                disabled: [
                    { required: true, message: '请选择学生状态', trigger: 'change' }
                ],
            },
            tableHead: [
                { title: 'ID', prop: 'id', minWidth: '100', },
                { title: '学生姓名', prop: 'student_name', minWidth: '120', },
                { title: '性别', prop: 'sex_name', minWidth: '100', },
                { title: '班级', prop: 'class_name', minWidth: '100', },
                { title: '身份证号', prop: 'id_card', minWidth: '180', },
                { title: '学籍号', prop: 'student_code', minWidth: '120', },
                { title: '家长姓名', prop: 'guardian_name', minWidth: '120', },
                { title: '联系电话', prop: 'guardian_tel', minWidth: '150', },
            ],
            tableData: [],
            hash: '',
            gradeList: [],
            classList: [],
            num: [],
        };
    },
    mounted() {
        this.getList();
        this.getHash();
    },
    methods: {
        checkPortrait(row) {
            this.portraitVisible = true;
            this.num = [];
            for (let index = 0; index < 5; index++) {
                this.num.push(Math.floor(Math.random() * (10 - 5 + 1)) + 5)
            }
            this.$nextTick(() => {
                this.getPortrait();
            })
        },
        getPortrait() {
            const chart = this.$refs.portrait;
            let myChart = this.$echarts.init(chart);
            if (myChart == null) {
                myChart = this.$echarts.init(chart);;
            }
            const option = {
                // title: {
                //     text: '画像信息'
                // },
                tooltip: {
                    trigger: 'axis'
                },
                legend: {
                    left: 'center',
                    data: [
                        '思想道德',
                        '劳动实践',
                        '文化知识',
                        '身心健康',
                        '审美素质'
                    ]
                },
                radar: {
                    // shape: 'circle',
                    indicator: [
                        { name: '思想道德', max: 10 },
                        { name: '劳动实践', max: 10 },
                        { name: '文化知识', max: 10 },
                        { name: '身心健康', max: 10 },
                        { name: '审美素质', max: 10 },
                    ],
                },
                series: [
                    {
                        type: 'radar',
                        tooltip: {
                            trigger: 'item'
                        },
                        areaStyle: {},
                        data: [
                            {
                                value: this.num,
                                name: '画像信息'
                            },
                        ]
                    }
                ]
            };
            //如果为空 则正常进行渲染 反之 不再进行初始化 
            option && myChart.setOption(option)
        },


        serchList() {
            this.pageNum = 1;
            this.pageSize = 15;
            this.getList()
        },
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.pageNum = 1;
            this.pageSize = newSize;
            this.getList();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.pageNum = newPage;
            this.getList();
        },
        checkInfo(row) {
            this.dialogVisible = true;
            this.$nextTick(() => {
                this.getBasicInfo(row.id);
            })
        },
        checkArchives(row) {
            localStorage.setItem('viewFile_id', row.id)
            this.$router.push({ name: 'viewFile' })
        },
        async getBasicInfo(id) {
            const res = await this.axios.post(archivesManagementArchivesBasicInfo, { student_id: id });
            if (res.data.code == 1) {
                this.formData = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async getList() {
            let data = {
                curr: this.pageNum,
                pageSize: this.pageSize,
                class_name: this.search.class_name,
                student_name: this.search.student_name,
            };
            const res = await this.axios.post(archivesManagementList, data);
            if (res.data.code == 1) {
                this.tableData = res.data.data.data;
                this.total = res.data.data.total;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        //获取Hash
        async getHash() {
            const res = await this.axios.post(hashApi, { page_hash: 'archivesManagement' });
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error',
                });
            }
        },
        async getInfo(id) {
            const res = await this.axios.post(studentManagementInfo, { id: id });
            if (res.data.code == 1) {
                this.formData = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        resetForm() {
            this.formData = {};
            this.$nextTick(() => {
                this.$refs['formData'].resetFields();
            })
        },
    }
};
</script>

<style >
.archivesManagement .el-table__row--level-1 {
    background: #fdfafa !important ;
}
</style>
<style lang="scss" scoped>
.archivesManagement {
    .searchA {
        display: flex;
        padding: 12px 25px;
        background: #fff;
        .el-select {
            margin-right: 24px;
        }
        .el-input {
            width: auto;
            margin-right: 24px;
        }
        .add {
            flex: 1;
            text-align: right;
        }
        .searchBtn {
            border: solid 1px #2f39ff;
            color: #2f39ff;
        }
    }
    .btn {
        width: 250px;
    }
    /deep/ .el-dialog {
        margin-top: 5vh !important;
    }
}
</style>



